@mixin desktop(){
	@media (min-width: 1367px){
		@content
	}
}

@mixin ipadPro2{
	@media (max-width: 1366px){
		@content
	}
}

@mixin ipadPro{
	@media (max-width: 1024px){
		@content
	}
}

@mixin ipad{
	@media (max-width: 768px){
		@content
	}
}

@mixin iphone8{
	@media (max-width: 414px){
		@content
	}
}

@mixin iphone5{
	@media (max-width: 320px){
		@content
	}
}

/*nav*/
.navbar li:nth-child(2) a{
	color: #563529;
	@include iphone8(){
		color: #eee;
	}
}
/*banner start*/
.bg{
	width: 100%;
	background-image: url('../image/bg/dessert.png');
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: 0 -100px;
	margin-top: 95px;
	@include desktop(){
		margin-top: 110px;
	}
	@media (max-width: 823px){
		margin-top: 65px;
	}
	@include iphone8(){
		margin-top: 80px;
	}
}
.bg figure{
	width: 50%;
	display: block;
	margin: auto;
	@include desktop(){
		width: 60%;
	}
	@include iphone8(){
		width: 100%;
	}
}
.bg img{
	width: 100%;
	vertical-align: middle;
	margin: auto;
}
/*banner end*/

/*====================================*/

/*main start*/
.news{
	width: 100%;
	padding: 10px;
	background-color: #452b21;
	div{
		margin-left: 75px;
	}
	h3{
		padding: 0 10px;
		color: #DECDA4;
		font-size: 16px;
		font-weight: normal;
		font-family: Microsoft JhengHei;
		display: inline-block;
		border-right: 1px solid #ddd;
	}
	marquee{
		vertical-align: middle;
    width: 300px;
		margin-left: 2px;
		padding: 0 5px;
		font-family: Microsoft JhengHei;
		font-size: 16px;
		color: #fff;
	}
	@include desktop(){
		h3{
			font-size: 24px;
		}
		marquee{
			font-size: 24px;
		}
	}
	@include ipadPro(){
		div{
			margin-left: 12px;
		}
	}
	@media (max-width: 823px){
		h3{
			font-size: 14px;
		}
		marquee{
			font-size: 14px;
		}
	}
	@include ipad(){
		div{
			margin-left: 15px;
		}
		h3{
			font-size: 14px;
		}
		marquee{
			font-size: 14px;
		}
	}
	@include iphone8(){
		div{
			margin: 0;
		}
		marquee{
			width: 200px;
		}
	}
	@include iphone5(){
		h3{
			font-size: 12px;
		}
		marquee{
			width: 180px;
			font-size: 12px;
		}
	}
}
.mainArea{
	width: 100%;
	background: url(../image/bg/bg_main.png);
	padding: 20px;
	@media (max-width: 1200px){
		padding: 20px 0px;
	}

}
.clearfix{
	clear: both;
}
.mainBox{
	width: 90%;
	margin: auto;
	@include ipadPro(){
		width: 100%;
	}
	@media (max-width: 823px){
		width: 100%;
	}
	
}

/*titleBox start*/
.titleBox{
	width: 19%;
	padding: 10px;
	margin-right: 10px;
	float: left;
	ul{
		li{
			cursor: pointer;
		}
	}

	@media (max-width: 823px){
		margin-left: 5px;
	}
	@include ipad(){
		float: unset;
		width: 100%;
		height: 180px;
		margin: auto;
		margin-top: 30px;
		padding: 0 30px;
		h2:after{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0px;
			width: 160px;
			border-bottom: 2px solid #3B1614;
		}
	}
	@include iphone8(){
		padding: 0 15px;
	}
}
.dessert{
	position: relative;
	padding: 20px 0;
	em{
		font-family: 'Dosis', sans-serif;
		font-style: unset;
		font-weight: normal;
	}
	strong{
		display: block;
		font-size: 18px;
		letter-spacing: 5px;
		font-family: Microsoft JhengHei;
		font-weight: normal;
	}
	@include desktop(){
		em{
			font-size: 38px;	
		}
		strong{
			font-size: 22px;
		}
	}
	@include ipadPro(){
		padding: 20px;
	}
	@media (max-width: 823px){
		em{
			font-size: 24px;
		}
		strong{
			font-size: 16px;
		}
	}
	@include ipad(){
		width: 180px;
		padding: 0;
		margin-left: 5px;
		strong{
			display: inline-block;
		}
	}
}

.side_menu{
	margin-top: 30px;
	overflow: hidden;
}
.side_menu .present{
	width: 170px;
	h4{
		width: 100%;
		padding: 8px 0;
		margin: 5px 0;
		color: #fff;
		font-family:Microsoft JhengHei;
		font-size: 16px;
		font-weight: normal;
		text-align: center;
		border-radius: 5px;
		background-color: #452b21;
		transition: all .5s;
		a{
			color: #fff;
		}
	}
	h4:hover{
		transition: all .5s;
		background-color: #7C1924 !important;
	}
	@include desktop(){
		width: 208px;
		h4{
			font-size: 20px;
			padding: 12px 0;
		}
	}
	@media (max-width: 823px){
		width: 134px;
		h4{
			font-size: 15px;
		}
	}
	@include ipad(){
		width: 140px;
		margin: 0 5px;
		float: left;
	}
	@media (max-width: 640px){
		width: 130px;
		h4{
			font-size: 14px;
			padding: 5px 0;
		}
	}
	@media (max-width: 568px){
		width: 110px;
	}
	@include iphone8(){
		width: 133px;
		h4{
			padding: 10px 0;
		}
	}
	
}
.prod li{
	width: 100%;
	letter-spacing: 1.3px;
	a{
		display: block;
    font-size: 16px;
    color: #544540;
    border-radius: 5px;
    padding: 5px;
    transition: all .2s linear;
	}
	
	a:hover{
		background: #888;
		color:#fff;
	}
	a:before{
		content: "";
		width: 3px;
    height: 3px;
		margin-left: 5px;
    margin-right: 10px;
    display: inline-block;
    vertical-align: middle;
    background: #544540;
    border-radius: 100%;
	}
	a:hover:before{
		background:#fff;
	}
	a.active{
		background: #888;
	  color: #fff;
	}
	a.active:before{
		background:#fff;
	}
	@include desktop(){
		a{
			font-size: 20px;
		}
	}
	@media (max-width: 823px){
		a{
			font-size: 14px;
		}
	}
	@include ipad(){
		background: #ddd;
		border-radius: 5px;
		margin: 2px 0;
	}
	@media (max-width: 640px){
		a{
			font-size: 12px;
		}
	}
	@include iphone8(){
		a{
			padding: 10px 8px;
		}
	}
} 
/*titleBox end*/

