.newProd{
	width: 100%;
	margin: auto;
	padding: 0 20px;
	@include iphone8(){
		padding: 0;
	}
}
.prod_box{
	width: 100%;
	margin: auto;
	padding: 20px 10px;
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
	@include iphone8(){
		display: block;
		padding: 20px 0;
		overflow: hidden;
	}
}
.prod_box .item{
	width: 30.333333%;
	margin: 1.5%;
	padding: 10px 10px 15px;
	box-shadow: 1px 1px 5px rgba(0,0,0,.4);
	border-radius: 5px;
	position: relative;
	overflow: hidden;
	background: #ddd;
	h4{
		font-size: 18px;
		text-align: center;
		color: #452b21;
		margin-top: 15px;
	}
	p{
		text-align: center;
		font-size: 14px;
		color: #452b21;
		margin: auto;
		padding: 5px 0;
	}
	span{
		display: block;
		width: 100%;
		text-align: center;
		font-size: 16px;
		padding: 5px 0 15px;
		color: #333;
	}
	@include desktop(){
		h4{
			font-size: 26px;
		}
		p{
			font-size: 20px;
		}
		span{
			font-size: 20px;
		}
	}
	@media (max-width: 640px){
		h4{
			font-size: 16px;
		}
		p{
			font-size: 12px;
		}
	}
	@include iphone8(){
		width: 46.666666%;
		float: left;
		margin: 3px 3px;
		margin-left: 6px;
		padding: 5px 0;
		background: #ddd;
		h4{
			font-size: 18px;
		}
		p{
			font-size: 14px;
		}
	}
	@media (max-width: 360px){
		h4{
			font-size: 16px;
		}
		p{
			font-size: 12px;
		}
	}
	@include iphone5(){
		h4{
			font-size: 14px;
		}
		p{
			font-size: 12px;
		}
	}
}
.prod_box .item .mask{
		display: block;
		width: 100%;
		height: 100%;
		opacity: 0;
		transition: .5s;
		border-radius: 5px;
		background: rgba(0,0,0,.4);
		position: absolute;
		/*top: 30%;*/
		top: 0;
		left: 0;
}
.prod_box .item .mask:before{
		content: '';
		display: block;
		width: 0%;
		height: 0%;
		border: 1px solid;
		border-color:  #eee  #eee transparent transparent;
		position: absolute;
		top: 5%;
		right: 5%;
		transition: 1s;
		z-index: 0;
}
.prod_box .item .mask:after{
	content: '';
	display: block;
	width: 0%;
	height: 0%;
	border: 1px solid;
	border-color: transparent transparent #eee  #eee;
	position: absolute;
	bottom: 5%;
	left: 5%;
	transition: 1s;
	z-index: 0;
}
.prod_box .item .mask span{
	position: absolute;
	color: #fff;
	margin-top: 100px;
	transition: .3s;
	opacity: 0;
}
.prod_box .item .mask span:nth-of-type(1){
	top: 70%;
	font-size: 20px;
	font-family: '標楷體', sans-serif;
	transition: all .5s;
	opacity: 0;
	@include desktop(){
		font-size: 28px;
		top: 74%;
	}
	@include ipadPro(){
		top: 68%;
	}	
	@media (max-width: 736px){
		font-size: 18px;
	}
	@media (max-width: 640px){
		font-size: 16px;
		top: 65%;
	}
	@include iphone8(){
		font-size: 16px;
	}
	@include iphone5(){
		font-size: 14px;
	}
}
.prod_box .item .mask span:nth-of-type(2){
	top: 78%;
	font-family: 'Vibur', cursive;
	font-size: 16px;
	text-align: center;
	transition: all .5s;
	opacity: 0;
	@include desktop(){
		font-size: 20px;
		top: 83%;
	}
	@include ipadPro(){
		top: 77%;
	}
	@media (max-width: 736px){
		font-size: 16px;
	}
	@media (max-width: 640px){
		font-size: 16px;
		top: 75%;
	}
	@include iphone8(){
		font-size: 14px;
	}
	@include iphone5(){
		font-size: 12px;
	}
}
.prod_box .item:hover .mask{
	opacity: 1;
	/*top: 0;*/
}
.prod_box .item:hover .mask span{
	opacity: 1;
	margin-top: 0;
	transition: .3s;
}
.prod_box .item:hover figure img{
	transition: all .5s;
	transform: scale(1.2);
	z-index: 99;
}
.prod_box .item:hover h4{
	opacity: 0;
}
.prod_box .item:hover p{
	opacity: 0;
}
.prod_box .item:hover span{
	opacity: 0;
}
.prod_box .item:hover .mask:before{
	width: 95%;
	height: 95%;
}
.prod_box .item:hover .mask:after{
	width: 95%;
	height: 95%;
}
.prod_box .item:hover .mask span{
	transition: all .5s;
	opacity: 1;
}
.fig{
	width: 85%;
	margin: auto;
	position: relative;
	z-index: 0;
	border-radius: 10px;
	z-index: 2;
}
.fig img{
	transition: all .5s;
